.modals { position: fixed; top: 50%; left: 50%; transform: translate(-50%,-50%); z-index: 2000000; height: 500px; width: 40%; margin: 0 auto; border-radius: 10px;}
.modals .imagewraps { height: 90%;}
.modals .imagewraps img {width: 100%; height: 100%; object-fit: cover;}
.modals .buttonwraps {height: 10%;}
.modals .buttonwraps button {border: none; background-color:antiquewhite;}
